export default {
	name: "tuiGridItem",
	props: {
		cell: {
			type: Number,
			default: 3
		},
		bgcolor: {
			type: String,
			default: "#fff"
		},
		//是否有点击效果
		hover: {
			type: Boolean,
			default: true
		},
		//底部线条
		bottom: {
			type: Boolean,
			default: true
		},
		index: {
			type: Number,
			default: 0
		}
	},
	methods: {
		handleClick() {
			this.$emit('click', {
				index: this.index
			});
		}
	}
}
